$(function () {
});

angular.module("MyApp", ["PageM", "ToolM", "cfp.hotkeys"])
         .controller("MyController", ["$scope", "PageProvider", "ToolProvider", "hotkeys", function ($scope, PageProvider,TPM, hotkeys) {
             $scope.PM = PageProvider;
             $scope.TM = {};
             $scope.VM = {};
             $scope.VM.title = "";
             $scope.VM.publishState = "0";

             $scope.changePage = function (pageIndex, noHistory) {
                 $("#tmBlock").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};
                 postData.D = $scope.VM.title;
                 postData.p = $scope.VM.publishState;
                 postData = $scope.PM.filterPageData(pgData, postData);
                 $scope.PM.changePage("/System/CodeM/CodeSettingOP.aspx", postData, function (data) {
                     $scope.TM.data = data.message;
                     $scope.$apply(function () { });
                 });
             };

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT',"BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one")
                     {
                         $scope.changePage(1);
                     }
                 }
             });

             $scope.goDetail = function (record) {
                 var data = $scope.save() || "";

                 var obj = {};
                 obj['i'] = record["I"];
                 obj['hash'] = encodeURIComponent(data);
                 TPM.doPOST("/System/CodeM/CodeSetting_Detail.aspx", obj,document.querySelector('#MyForm'));
             };

             //$scope.doReset = function () {
             //    $("#tmBlock").hide();
             //    $scope.PM.reset();
             //};

             $scope.save = function () {
                 var data = {};
                 data['pgNow'] = $scope.PM.pgNow;
                 TPM.setData(data);
                 return JSON.stringify(data);
             };

             if (location.hash.length > 0) {
                 var data = TPM.getData();
                 $scope.changePage(data.pgNow);
             }
             else {
                 $scope.changePage(1);
             }
}]);
